<template>
  <div :class="$style.app__loader">
    <img :class="$style.loader__spinner" src="./assets/loader__spinner.svg">
  </div>
</template>

<script>
  export default {
    name: 'app-loader',
  }
</script>

<style lang="scss" module>
  .app__loader {
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 100%;
    background-color: #fff;
  }

  .loader__spinner {
    display: block;
    width: 10%;
    height: auto;
    margin: 0 auto;
    vertical-align: middle;

  }
</style>


